import { evntBus } from "../../bus";
import ItemsSelector from "./ItemsSelector.vue";
import ItemsCrads from "./ItemsCrads.vue";
import OpeningDialog from "./OpeningDialog.vue";

export default {
  data: function () {
    return {
      dialog: false,
      pos_profile: "",
      pos_opening_shift: "",
    };
  },

  components: {
    ItemsSelector,
    ItemsCrads,
    OpeningDialog,
  },

  methods: {
    check_opening_entry() {
      return frappe
        .call("posawesome.posawesome.api.posapp.check_opening_shift", {
          user: frappe.session.user,
        })
        .then((r) => {
          if (r.message) {
            this.pos_profile = r.message.pos_profile;
            this.pos_opening_shift = r.message.pos_opening_shift;
            evntBus.$emit("register_pos_profile", r.message.pos_profile);
            console.log("LoadPosProfile")
          } else {
            this.create_opening_voucher();
          }
        });
      // frappe.call({
      //   method: "posawesome.posawesome.api.posapp.check_opening_shift",
      //   args: { user: frappe.session.user },
      //   async: false,
      //   callback: function (r) {
      //     if (r.message) {
      //       if (r.message) {
      //         this.pos_profile = r.message.pos_profile;
      //         this.pos_opening_shift = r.message.pos_opening_shift;
      //         evntBus.$emit("update_items", r.message.pos_profile);
      //       } else {
      //         this.create_opening_voucher();
      //       }
      //     }
      //   },
      // });
    },
    create_opening_voucher() {
      console.log("create_opening_voucher");
      this.dialog = true;
    },
  },

  created: function () {
    this.$nextTick(function () {
      this.check_opening_entry();
      evntBus.$on("close_opening_dialog", () => {
        this.dialog = false;
      });
      evntBus.$on("register_pos_data", (data) => {
        this.pos_profile = data.pos_profile;
        this.pos_opening_shift = data.pos_opening_shift;
        evntBus.$emit("register_pos_profile", data.pos_profile);
        console.log("LoadPosProfile")
      });
    });
  },
};
